<template>
    <div div id="app" v-bind:class="girdCurrent">
        <section class="playlist" id="playlist" :class="{maxheight: lyric.length !== 0}">
        <div class="kyr-title-playlist">
          <h3>Now Playing <span> 🎵 </span></h3>
        </div>
        <div class="form">
          <input v-model="search" type="text" placeholder="search music ..." @keypress="searchSong"/>
        </div>
        <div class="kyr-music-type" v-show="searched === true">
          <div class="kyr-list-song-current">
            <div class="typeContry">
              <h4>Chart RealTime</h4>
              <div class="show-more" name="Show More" type="button" v-show="more === true" v-on:click="more = false">Show More</div>
              <div class="show-more" name="Back" type="button" v-show="more === false" v-on:click="more = true">Back</div>
            </div>
            <div class="country">
              <ul class="kyr-list">
                <li @click="chartRealTime = chartRealTimeVN">Việt Nam</li>
                <li @click="chartRealTime = chartRealTimeUSUK">US-UK</li>
                <li @click="chartRealTime = chartRealTimeKP">K-POP</li>
              </ul>
            </div>
            <ul class="kyr-music">
              <li class="component-music" v-for="(song,index) in chartRealTime" :key="index" @click="setSong(index,0)" v-show="more === true ? index < 10 : index >= 0">
                <div class="kyr-image-song">
                  <img :src='song.image[1]'  :alt='song.title'/>
                </div>
                <div class="kyr-detail-song">
                  <h4>{{song.title}}</h4>
                  <p class="singer">{{song.artist.name}}</p>
                  <KProgress v-show="song.id === current.id"
                    :show-text="false"
                    class="progress-bar-wrapper"
                    v-bind:percent="percent"
                    :color="['#df83f1', '#82279f', '#53cfe0']"
                  />
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div class="kyr-list-song" v-show="searched === false">
          <div class="title"><h4>Kết quả tìm kiếm: {{result}}</h4></div>
          <div class="title" v-if="listSong.length === 0"><h4>Không có kết quả được tìm thấy 😃😃</h4></div>
          <ul class="kyr-music" v-else>
            <li class="component-music" v-for="(song,index) in listSong" :key="index" @click="setSong(index,1)">
                <div class="kyr-image-song">
                  <img :src='song.image[1]' :alt='song.title'/>
                </div>
                <div class="kyr-detail-song">
                  <h4>{{song.title}}</h4>
                  <p class="singer">{{song.artist.name}}</p>
                  <KProgress v-show="song.id === current.id"
                  :show-text="false"
                  class="progress-bar-wrapper"
                  v-bind:percent="percent"
                  :color="['#df83f1', '#82279f', '#53cfe0']"
                  />
                </div>
            </li>
          </ul>
        </div>
      </section>
      <main v-if="current.length !== 0">
          <div class="kyr-cover" v-show="openListSong === false"></div>
          <section class="player" id="player">
            <div class="kyr-list-tracks">
              <div class="kyr-icon" :class="{change: openListSong === false}" @click="openListSong = !openListSong">
                <div class="bar-1"></div>
                <div class="bar-2"></div>
                <div class="bar-3"></div>
              </div>
              <div class="kyr-container" :class="{open: openListSong === false}">
                <div class="title"><h4>Danh Sách Nhạc Hiện Tại</h4></div>
                  <ul class="kyr-music">
                    <li class="component-music" v-for="(song,index) in listSongCurrent" :key="index" @click="setSong(index,2)">
                        <div class="kyr-image-song">
                          <img :src='song.image[1]' :alt='song.title'/>
                        </div>
                        <div class="kyr-detail-song">
                          <h4>{{song.title}}</h4>
                          <p class="singer">{{song.artist.name}}</p>
                          <KProgress v-show="song.id === current.id"
                          :show-text="false"
                          class="progress-bar-wrapper"
                          v-bind:percent="percent"
                          :color="['#df83f1', '#82279f', '#53cfe0']"
                          />
                        </div>
                    </li>
                </ul>
              </div>
            </div>
            <div class="kyr-image">
                <img :src='current.image[0]' :alt='current.title' />
            </div>
            <div class="kyr-song-details">
              <div class="kyr-title">
                <h4 class="kyr-name-song">{{current.title}}</h4>
                <p class="kyr-singer">{{current.artist.name}}</p>
              </div>
              <div class="kyr-lyric" v-if="lyric.length !== 0">
                <p class="kyr-text" :class="{lyricCurrent: _lyric.start <= player.currentTime && _lyric.end >= player.currentTime, lyricCurrented: player.currentTime >= _lyric.end}" 
                v-for="(_lyric,index) in lyric.lyrics" 
                :key="index"
                >{{_lyric.text}}</p>
              </div>
              <div class="kyr-control-track" style="cursor: pointer">
                <input type="range" min="1" max="100" name = "percent"
                v-model="value" class="seek_slider" @change="control">
                <KProgress
                :show-text="false"
                class="progress-bar-wrapper"
                v-bind:percent="percent"
                :color="['#df83f1', '#82279f', '#53cfe0']"
                />
              </div>
              <div class="kyr-timer">
                <p class="start">{{currentTime}}</p>
                <p class="end">{{currenTimeEnd(current.duration)}}</p>
              </div>
            </div>
            <div class="kyr-control">
              <button class ="Prev" @click="Prev()" name="Prev">
                <font-awesome-icon icon="step-backward" />
              </button>
              <button @click="Play()" class ="Play" name="Play" type="button" v-if="playing === false">
                 <font-awesome-icon icon="play"/>  
              </button>
              <button v-on:click="Pause" class ="Pause" name="Pause" type="button" v-else>
                <font-awesome-icon icon="pause"/>
              </button>
              <button class ="Next" @click="Next()" name="Next" type="button">
                <font-awesome-icon icon="step-forward"/>
              </button>
              <div class="Redo" @click="Redo()" name="Redo">
                <font-awesome-icon icon="redo"/>
                <div class="Times">
                  <font-awesome-icon icon="times" v-if="next === true"/>
                  <p v-else>1</p>
                </div>
              </div>
            </div>
          </section>
      </main>
    </div>
</template>

<script>

import KProgress from "k-progress"
import Vue from 'vue'
import AudioVisual from 'vue-audio-visual'
// import Song from '../public/song/ZKOAK980.mp3'
Vue.use(AudioVisual)

import {formatTimer} from '../assets/js/timer.js'
import axios from 'axios'
export default {
  components: { KProgress },
  name: 'Home',
  data(){
      return{
          search: '',
          listSong: [],
          chartRealTime: [],
          chartRealTimeVN: [],
          chartRealTimeUSUK: [],
          chartRealTimeKP: [],
          listSongCurrent: [],
          current: [],
          lyric: {},
          currentTime: '00:00',
          index: 0,
          playing: false,
          next: true,
          more: true,
          player: null,
          searched: true,
          result: '',
          openListSong: true,
          percent: 0,
          value: 0,
          indexLyric: 0,
          timer: 0,
          timer_: null
      }
  },  
  mounted(){ 
      axios.get(`https://reef-songeth.herokuapp.com/api/song-near/chart-realtime/VN`)
      .then( res =>
          {
            this.chartRealTimeVN = res.data.body
            this.listSongCurrent = this.chartRealTimeVN
            this.chartRealTime = this.chartRealTimeVN
          }
      )
      .catch(err=>{
        console.log(err)
      })

      axios.get(`https://reef-songeth.herokuapp.com/api/song-near/chart-realtime/USUK`)
      .then( res =>
          {
            this.chartRealTimeUSUK = res.data.body
            this.listSongCurrent = this.allTracks
          }
      )
      .catch(err=>{
        console.log(err)
      })

      axios.get(`https://reef-songeth.herokuapp.com/api/song-near/chart-realtime/KP`)
      .then( res =>
          {
            this.chartRealTimeKP = res.data.body
            this.listSongCurrent = this.allTracks
          }
      )
      .catch(err=>{
        console.log(err)
      })
  },
  methods:{
      currenTimeEnd(second){
        return  formatTimer(second)
      },
      listenWhenPlay(){
        
        this.player.addEventListener("timeupdate",()=>{
            this.currentTime = formatTimer(this.player.currentTime)
            this.percent = (this.player.currentTime*100)/this.current.timer
        })
       
        this.player.addEventListener("ended",()=>{
          if(this.next === true){
            this.increaseView()
            this.current.playing = true
            this.Next()
          }
          else{
            this.increaseView()
            this.current.playing = true
            this.Play()
          }
        })
      },
      setCurrentSong(k){
          if(this.playing === true){
            this.player.pause()
            this.player = null
          }
          
          this.current = this.listSongCurrent[k]
          
          axios.post('https://reef-songeth.herokuapp.com/api/lyric',{
            data:{
              key: this.current.id
            }
          })
          .then(res=>{
            if(res.data.body !== false){
              this.lyric = res.data.body[0]
            }else{
              this.lyric = []
            }
          })
          .catch(err=>{
            console.log(err)
          })
          this.Play()
      },
      Play(){
        if(this.current.playing === true){
          this.player = null
          this.current.playing = false
          this.index = this.listSongCurrent.indexOf(this.current);
          // this.player = new Audio(Song);
          this.player = new Audio(this.current.url);
        }
        // console.log(this.player)
        var playPromise = this.player.play();

        if (playPromise !== undefined) {
          playPromise.then(() => {
            setTimeout(()=>{

            },this.player.duration*1000)
          })
          .catch(error => {
            console.log(error)
          });
        }
        clearInterval(this.timer_);
        this.timer = 0
        this.playing = true
        this.listenWhenPlay()

      },
      
      Pause(){
        this.playing = false
        this.player.pause()

        this.timer_ = setInterval(()=>{
          this.timer++
          if(this.timer === 720){
            location.reload();
          }
        },1000)
      
      },

      Prev(){
        
        this.player.pause()
        this.current.playing = true
        this.percent = 0
        this.index -= 1
        
        if(this.index < 0){
          this.index = this.listSongCurrent.length-1
        }
        
        this.setCurrentSong(this.index)
      },

      Next(){
        this.player.pause()
        this.current.playing = true
        this.percent = 0
        this.index += 1
        
        if(this.index > this.listSongCurrent.length-1){
          this.index = 0
        }
        
        this.setCurrentSong(this.index)
      },

      setSong(k,index){
        this.current.playing = true
        if (index === 1){  
          this.current = this.listSong[k]
          this.listSongCurrent = []
          this.listSongCurrent.push(this.current)
          
          if(this.current.country === 'Việt Nam'){
            this.listSongCurrent = this.listSongCurrent.concat(this.chartRealTimeVN.filter(track=>{
            return (track.artist.name === this.current.artist.name || track.country === this.current.country) && this.current.id !== track.id}))
          }
          else if(this.current.country === 'US-UK'){
            this.listSongCurrent = this.listSongCurrent.concat(this.chartRealTimeUSUK.filter(track=>{
            return (track.artist.name === this.current.artist.name || track.country === this.current.country) && this.current.id !== track.id}))
          }
          else{
            this.listSongCurrent = this.listSongCurrent.concat(this.chartRealTimeKPOP.filter(track=>{
            return (track.artist.name === this.current.artist.name || track.country === this.current.country) && this.current.id !== track.id}))
          }
      
          k = 0
        }
        else if(index === 0){
          this.listSongCurrent = this.chartRealTime
          this.current = this.listSongCurrent[k]
        }
        else{
          this.current = this.listSongCurrent[k]
        }
        
        this.percent = 0
        this.index = k;
        this.setCurrentSong(this.listSongCurrent.indexOf(this.current))
      },

      Redo(){
        this.next = !this.next
      },

      searchSong(e){
        if (e.key === 'Enter'){
          if (this.search === ''){
            this.searched = true         
          }
          else{
            this.result = this.search
            axios.post(`https://reef-songeth.herokuapp.com/api/search-tracks`,{
              data:{
                key: this.search
              }
            })
            .then(res => {
              if(res.data.body !== false){
                this.listSong = res.data.body
              }else{
                this.listSong = []
              }
            })
            this.searched = false
          }
        }
      },

      increaseView(){
        axios.post('https://reef-songeth.herokuapp.com/api/song-near/chart-realtime/increase',{
          data:{
            id: this.current.id,
            view: this.current.view + 1
          }
        })
        .then(res=>{
          console.log(res.data.body)
        })
        .catch(err=>{
          console.log(err)
        })
        this.current.view++;
      },

      control(){
        this.player.currentTime = (this.value*this.current.duration)/100;
      }
    },
    computed:{
      girdCurrent(){
        if (this.current.length === 0){
          return 'none-gird'
        }
        return 'gird'
      },
    }
}
</script>

<style>
  @import '../assets/style/style.css';
  @import '../assets/style/playlist.css';
</style>
